<script>
import {ref,watchEffect,onMounted} from 'vue';
export default {
  name: 'contact',
  props: {
    position:Number,
    contact_pos:Number
	},
  setup(props){
    onMounted(() => {
			contact_dom.value = document.getElementById("contacto").offsetTop;
		});

    const contact_dom = ref(0);
    const contact = ref(false);

    watchEffect(() => {
      if(props.position >= (contact_dom.value + props.contact_pos - 300)){
        contact.value = true;
      } else{
        contact.value = false;
      }
    });
    
    return{contact}
  }
}

