<template>

  <div class="z-5 fixed top-0 flex w-full shadow-5 h-5rem xl:h-6rem" style="background-color: #ffffff;">
    <div class="px-3 justify-content-center flex align-items-center w-full relative">
      <img @click="$emit('nav_click','home')" class="cursor-pointer w-11rem md:w-15rem" src="@/assets/logo_alemania.png" alt="Logo Alemania">
      <!-- Nav item list pages -->
      <div class="hidden lg:flex justify-content-center xl:ml-5 ml-2 align-items-center h-full list">
        <div @mouseover="hover_color('home')" @mouseleave="leave_color('home')" @click="$emit('nav_click','home')" class="home select-none button-link transition-colors transition-duration-500 h-full text-base xl:text-xl px-2 xl:px-3 flex align-items-center cursor-pointer">INICIO</div><div class="d-one transition-colors transition-duration-200" style="width: 1px;height:40px;"></div>
        <div @mouseover="hover_color('about')" @mouseleave="leave_color('about')" @click="$emit('nav_click','about')" class="about select-none button-link transition-colors transition-duration-500 h-full text-base xl:text-xl px-2 xl:px-3 flex align-items-center cursor-pointer">QUIÉNES SOMOS</div><div class="d-two transition-colors transition-duration-200" style="width: 1px;height:40px;"></div>
        <div @mouseover="hover_color('norms')" @mouseleave="leave_color('norms')" @click="$emit('nav_click','norms')" class="norms select-none button-link transition-colors transition-duration-500 h-full text-base xl:text-xl px-2 xl:px-3 flex align-items-center cursor-pointer">NORMATIVAS</div><div class="d-three transition-colors transition-duration-200" style="width: 1px;height:40px;"></div>
        <div @mouseover="hover_color('plans')" @mouseleave="leave_color('plans')" @click="$emit('nav_click','plans')" class="plans select-none button-link transition-colors transition-duration-500 h-full text-base xl:text-xl px-2 xl:px-3 flex align-items-center cursor-pointer">PLANES</div><div class="d-four transition-colors transition-duration-200" style="width: 1px;height:40px;"></div>
        <div @mouseover="hover_color('news')" @mouseleave="leave_color('news')" @click="$emit('nav_click','news')" class="news select-none button-link transition-colors transition-duration-500 h-full text-base xl:text-xl px-2 xl:px-3 flex align-items-center cursor-pointer">NOTICIAS</div><div class="d-five transition-colors transition-duration-200" style="width: 1px;height:40px;"></div>
        <div @mouseover="hover_color('course')" @mouseleave="leave_color('course')" @click="$emit('nav_click','course')" class="course select-none button-link transition-colors transition-duration-500 text-base xl:text-xl h-full px-2 xl:px-3 flex align-items-center cursor-pointer">CURSOS</div><div class="d-six transition-colors transition-duration-200" style="width: 1px;height:40px;"></div>
        <div @mouseover="hover_color('contact')" @mouseleave="leave_color('contact')" @click="$emit('nav_click','contact')" class="contact select-none button-link transition-colors transition-duration-500 text-base xl:text-xl h-full px-2 xl:px-3 flex align-items-center cursor-pointer">CONTACTO</div>
      </div>
      <div class="hidden lg:flex justify-content-center ml-2 xl:ml-5 align-items-center h-full">
        <a href="https://www.facebook.com/profile.php?id=100057475547188" class="cursor-pointer" target=”_blank”><i class="face icon pi pi-facebook"/></a>
        <a href="https://www.instagram.com/lenguajealemania_/" class="cursor-pointer mx-3" target=”_blank”><i class="insta icon pi pi-instagram"/></a>
      </div>
      <!-- Nav Item List right -->
      <div @click="$emit('side_menu')" class="cursor-pointer lg:hidden absolute" style="right:5%;">
        <i class="icon pi pi-bars"></i>
      </div>
    </div>
  </div>
</template>

<script>
import {ref,watchEffect} from 'vue';

export default {
  emits: ['nav_click','side_menu'],
	props: {
    position:Number,
    about_pos:Number,
    norms_pos:Number,
    plans_pos:Number,
    news_pos:Number,
    course_pos:Number,
    contact_pos:Number
	},
  setup(props){
      const c_home_text = ref("#434040");
      const c_about_text = ref("#434040");
      const c_norms_text = ref("#434040");
      const c_plans_text = ref("#434040");
      const c_news_text = ref("#434040");
      const c_course_text = ref("#434040");
      const c_contact_text = ref("#434040");

      const c_home = ref("#ff006900");
      const c_about = ref("#ffb54700");
      const c_norms = ref("#48c40000");
      const c_plans = ref("#00b6e800");
      const c_news = ref("#ff006900");
      const c_course = ref("#ffb54700");
      const c_contact = ref("#ffb54700");

      const c_one = ref("#434040");
      const c_two = ref("#434040");
      const c_three = ref("#434040");
      const c_four = ref("#434040");
      const c_five = ref("#434040");
      const c_six = ref("#434040");

      const hover_color = (elem) => {
        switch(elem){
          case "home":
            c_home.value = "#ff0069";
            c_one.value = "#43404000";
            c_home_text.value = "#ffffff";
            break;
          case "about":
            c_about.value = "#ffb547";
            c_about_text.value = "#ffffff"
            c_one.value = "#43404000";
            c_two.value = "#43404000";
            break;
          case "norms":
            c_norms.value = "#48c400";
            c_norms_text.value = "#ffffff"
            c_two.value = "#43404000";
            c_three.value = "#43404000";
            break;
          case "plans":
            c_plans.value = "#00b6e8";
            c_plans_text.value = "#ffffff"
            c_three.value = "#43404000";
            c_four.value = "#43404000";
            break;
          case "news":
            c_news.value = "#ff0069";
            c_news_text.value = "#ffffff"
            c_four.value = "#43404000";
            c_five.value = "#43404000";
            break;
          case "course":
            c_course.value = "#ffb547";
            c_course_text.value = "#ffffff"
            c_five.value = "#43404000";
            c_six.value = "#43404000";
            break;
          case "contact":
            c_contact.value = "#48c400";
            c_contact_text.value = "#ffffff"
            c_six.value = "#43404000";
            break;
          default:
            break;
        }
      }

      const leave_color = (elem) => {
        switch(elem){
          case "home":
            if (!(props.position < props.about_pos - 300)){
              c_home.value = "#ff006900";
              c_home_text.value = "#434040";
              if (!(props.position >= props.about_pos - 300 && props.position < props.norms_pos - 300)){
                c_one.value = "#434040";
              }
            }
            break;
          case "about":
            if (!(props.position >= props.about_pos - 300 && props.position < props.norms_pos - 300)){
              c_about.value = "#ffb54700";
              c_about_text.value = "#434040"
              if (!(props.position < props.about_pos - 300)){
                c_one.value = "#434040";
              }
              if (!(props.position >= props.norms_pos - 300 && props.position < props.plans_pos - 300)){
                c_two.value = "#434040";
              }
            }
            break;
          case "norms":
            if (!(props.position >= props.norms_pos - 300 && props.position < props.plans_pos - 300)){
              c_norms.value = "#48c40000";
              c_norms_text.value = "#434040"
              if (!(props.position >= props.about_pos - 300 && props.position < props.norms_pos - 300)){
                c_two.value = "#434040";
              }
              if (!(props.position >= props.plans_pos - 300 && props.position < props.news_pos - 300)){
                c_three.value = "#434040";
              }
            }
            break;
          case "plans":
            if (!(props.position >= props.plans_pos - 300 && props.position < props.news_pos - 300)){
              c_plans.value = "#00b6e800";
              c_plans_text.value = "#434040"
              if (!(props.position >= props.norms_pos - 300 && props.position < props.plans_pos - 300)){
                c_three.value = "#434040";
              }
              if (!(props.position >= props.news_pos - 300 && props.position < props.course_pos - 300)){
                c_four.value = "#434040";
              }
            }
            break;
          case "news":
            if (!(props.position >= props.news_pos - 300 && props.position < props.course_pos - 300)){
              c_news.value = "#ff006900";
              c_news_text.value = "#434040"
              if (!(props.position >= props.plans_pos - 300 && props.position < props.news_pos - 300)){
                c_four.value = "#434040";
              }
              if (!(props.position >= props.course_pos - 300 && props.position < props.contact_pos - 300)){
                c_five.value = "#434040";
              }
            }
            break;
          case "course":
            if (!(props.position >= props.course_pos - 300 && props.position < props.contact_pos - 300)){
              c_course.value = "#ffb54700";
              c_course_text.value = "#434040"
              if (!(props.position >= props.news_pos - 300 && props.position < props.course_pos - 300)){
                c_five.value = "#434040";
              }
              if (!(props.position - 300 >= props.contact_pos - 300)){
                c_six.value = "#434040";
              }
            }
            break;
          case "contact":
            if (!(props.position - 300 >= props.contact_pos - 300)){
              c_contact.value = "#48c40000";
              c_contact_text.value = "#434040"
              if (!(props.position >= props.course_pos - 300 && props.position < props.contact_pos - 300)){
                c_six.value = "#434040";
              }
            }
            break;
          default:
            break;
        }
      }

      const reset_colors = () =>{
        c_home_text.value = "#434040";
        c_about_text.value = "#434040";
        c_norms_text.value = "#434040";
        c_plans_text.value = "#434040";
        c_news_text.value = "#434040";
        c_course_text.value = "#434040";
        c_contact_text.value = "#434040";

        c_home.value = "#ff006900";
        c_about.value = "#ffb54700";
        c_norms.value = "#48c40000";
        c_plans.value = "#00b6e800";
        c_news.value = "#ff006900";
        c_course.value = "#ffb54700";
        c_contact.value = "#48c40000";

        c_one.value = "#434040";
        c_two.value = "#434040";
        c_three.value = "#434040";
        c_four.value = "#434040";
        c_five.value = "#434040";
        c_six.value = "#434040";
      }

      watchEffect(() => {
        switch(true){
          case props.position < props.about_pos - 300:
            reset_colors();
						c_home.value = "#ff0069";
            c_one.value = "#43404000";
            c_home_text.value = "#ffffff";
						break;
          case props.position >= props.about_pos - 300 && props.position < props.norms_pos - 300:
            reset_colors();
						c_about.value = "#ffb547";
            c_about_text.value = "#ffffff"
            c_one.value = "#43404000";
            c_two.value = "#43404000";
						break;
          case props.position >= props.norms_pos - 300 && props.position < props.plans_pos - 300:
            reset_colors();
						c_norms.value = "#48c400";
            c_norms_text.value = "#ffffff"
            c_two.value = "#43404000";
            c_three.value = "#43404000";
						break;
          case props.position >= props.plans_pos - 300 && props.position < props.news_pos - 300:
            reset_colors();
						c_plans.value = "#00b6e8";
            c_plans_text.value = "#ffffff"
            c_three.value = "#43404000";
            c_four.value = "#43404000";
						break;
          case props.position >= props.news_pos - 300 && props.position < props.course_pos - 300:
            reset_colors();
						c_news.value = "#ff0069";
            c_news_text.value = "#ffffff"
            c_four.value = "#43404000";
            c_five.value = "#43404000";
						break;
          case props.position >= props.course_pos - 300 && props.position < props.contact_pos - 300:
            reset_colors();
						c_course.value = "#ffb547";
            c_course_text.value = "#ffffff"
            c_five.value = "#43404000";
            c_six.value = "#43404000";
						break;
					default: 
            reset_colors();
            c_contact.value = "#48c400";
            c_contact_text.value = "#ffffff"
            c_six.value = "#43404000";
        }
      });
    return {c_home,c_about,c_norms,c_plans,c_news,c_course,c_one,c_two,c_three,c_four,c_five,hover_color,leave_color,c_home_text,c_about_text,
      c_norms_text,c_plans_text,c_news_text,c_course_text,c_six,c_contact,c_contact_text}
  }
}
</script>

<style scoped>

  .icon {
    font-size: 30px;
    color: #434040;
  }

  .insta:hover{
    color: #926458
  }

  .face:hover {
    color: #0B84ED;
  }

  .button-link{
    font-family: quicksand;
  }
  .home{
    background: v-bind(c_home);
    color: v-bind(c_home_text);
  }
  .about{
    background: v-bind(c_about);
    color: v-bind(c_about_text);
  }
  .norms{
    background: v-bind(c_norms);
    color: v-bind(c_norms_text);
  }
  .plans{
    background: v-bind(c_plans);
    color: v-bind(c_plans_text);
  }
  .news{
    background: v-bind(c_news);
    color: v-bind(c_news_text);
  }
  .course{
    background: v-bind(c_course);
    color: v-bind(c_course_text);
  }
  .contact{
    background: v-bind(c_contact);
    color: v-bind(c_contact_text);
  }
  .d-one{
    background-color: v-bind(c_one);
  }
  .d-two{
    background-color: v-bind(c_two);
  }
  .d-three{
    background-color: v-bind(c_three);
  }
  .d-four{
    background-color: v-bind(c_four);
  }
  .d-five{
    background-color: v-bind(c_five);
  }
  .d-six{
    background-color: v-bind(c_six);
  }

</style>
