<template>
  <div class="fondo py-4">
    <transition name="fade">
        <img v-if="course" class="kid-a hidden md:flex w-13rem lg:w-16rem xl:w-19rem" src="@/assets/kids/kid-09.png"/>
    </transition>
    <div id="cursos" class="main-content relative" >
      <transition name="fade">
        <h1 v-if="course" class="main-txt select-none text-5xl lg:text-6xl xl:text-7xl font-light">Cursos</h1>
      </transition>
      <transition name="fade">
        <div v-if="course" class="flex flex-wrap align-items-center justify-content-center mt-4 lg:mt-8">
          <img class="title" src="@/assets/cursos/0.png" />
          <div class="flex flex-wrap align-items-center justify-content-center mt-4 lg:mt-8">
            <img class="tag" src="@/assets/cursos/1.png" />
            <img class="tag" src="@/assets/cursos/2.png" />
            <img class="tag" src="@/assets/cursos/3.png" />
          </div>
          
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import {ref,watchEffect,onMounted} from 'vue';

export default {
  name: 'courses',
  props: {
    position:Number,
    course_pos:Number
	},
  setup(props){
    onMounted(() => {
			course_dom.value = document.getElementById("cursos").offsetTop;
      bottom_dom.value = document.getElementById("cursos").offsetHeight;
		});

    const course_dom = ref(0);

    const course = ref(false);
    const bottom_dom = ref(0);

    watchEffect(() => {
      if(props.position >= (course_dom.value + props.course_pos - 300) && props.position < (course_dom.value + props.course_pos + bottom_dom.value - 300)){
        course.value = true;
      } else{
        course.value = false;
      }
    });
    
    return{course}
  }
}

</script>

