<script>
import {ref,watchEffect,onMounted} from 'vue';

export default {
  name: 'plans',
  props: {
    position:Number,
    plans_pos:Number
	},
  setup(props){

    onMounted(() => {
			plan_dom.value = document.getElementById("plan").offsetTop;
      bottom_dom.value = document.getElementById("plan").offsetHeight;
		});

    const publicPath = ref(process.env.BASE_URL);

    const plan_dom = ref(0);
    const bottom_dom = ref(0);

    const plan = ref(false);

    watchEffect(() => {
      if(props.position >= (plan_dom.value + props.plans_pos - 300) && props.position < (plan_dom.value + props.plans_pos + bottom_dom.value - 300)){
        plan.value = true;
      }else{
        plan.value = false;
      }
    });

    return {plan,publicPath}
    
  }
}

</script>

