<template>
  <div v-on:scroll="on_scroll">
      <!-- Our Nav Bar Component and Side Menu -->
      <nav-bar :position="current_pos" :about_pos="about_dom" :norms_pos="norms_dom" :plans_pos="plans_dom"
              :news_pos="news_dom" :course_pos="course_dom" :contact_pos="contact_dom"
              @nav_click="go_to" @side_menu="display_menu"
      />
      <side-menu v-model:visible="show_menu" @nav_click="go_to"/>
      
      <!-- Main Content -->
      <div id="home" class="h-5rem xl:h-6rem"></div>
      <home-page :position="current_pos" :home_pos="home_dom" @nav_click="go_to"/>
      <div id="about" class="h-5rem xl:h-6rem"></div>
      <about-page :position="current_pos" :about_pos="about_dom"/>
      <div id="norms" class="h-5rem xl:h-6rem"></div>
      <norms-page :position="current_pos" :norms_pos="norms_dom"/>
      <div id="plans" class="h-5rem xl:h-6rem"></div>
      <plans-page :position="current_pos" :plans_pos="plans_dom"/>
      <div id="news" class="h-5rem xl:h-6rem"></div>
      <news-page :position="current_pos" :news_pos="news_dom"/>
      <div id="course" class="h-5rem xl:h-6rem"></div>
      <course-page :position="current_pos" :course_pos="course_dom"/>
      <div id="contact" class="h-5rem xl:h-6rem"></div>
      <contact-page :position="current_pos" :contact_pos="contact_dom"/>
      
  </div>
</template>

<script>
// Import our components from component directory
import {ref,onMounted} from 'vue';
import NavBar from '@/components/NavBar.vue';
import SideMenu from '@/components/Menu.vue';
import HomePage from '@/components/content/HomePage.vue';
import AboutPage from '@/components/content/AboutPage.vue';
import NormsPage from '@/components/content/NormsPage.vue';
import PlansPage from '@/components/content/PlansPage.vue';
import NewsPage from '@/components/content/NewsPage.vue';
import CoursePage from '@/components/content/CoursePage.vue';
import ContactPage from '@/components/content/ContactPage.vue';

// Use our components in our App.vue template
export default {
  name: 'app',
  components: {
    navBar: NavBar,
    sideMenu: SideMenu,
    homePage: HomePage,
    aboutPage: AboutPage,
    normsPage: NormsPage,
    plansPage: PlansPage,
    newsPage: NewsPage,
    coursePage: CoursePage,
    contactPage: ContactPage
  },
  setup(){
    onMounted(() => {
			window.addEventListener('scroll', on_scroll);
      home_dom.value = document.getElementById("home").offsetTop;
      about_dom.value = document.getElementById("about").offsetTop;
      norms_dom.value = document.getElementById("norms").offsetTop;
      plans_dom.value = document.getElementById("plans").offsetTop;
      news_dom.value = document.getElementById("news").offsetTop;
      course_dom.value = document.getElementById("course").offsetTop;
      contact_dom.value = document.getElementById("contact").offsetTop;
		});

    const home_dom = ref(0);
    const about_dom = ref(0);
    const norms_dom = ref(0);
    const plans_dom = ref(0);
    const news_dom = ref(0);
    const course_dom = ref(0);
    const contact_dom = ref(0);

    const show_menu = ref(false);

    const current_pos = ref(0);


    const display_menu = () => {
			show_menu.value = !show_menu.value;
		};

    const go_to = (refName) => {
      show_menu.value = false;
			let element = document.getElementById(refName);
      if (element) {
        const yOffset = -60; // Ajusta esto para el desplazamiento deseado
        const y = element.getBoundingClientRect().top + window.scrollY + yOffset;
        window.scrollTo({ top: y, behavior: 'smooth' });
      }
		};

    const on_scroll = (event) => {
      let elem = event.srcElement.scrollingElement
      let pos = elem.scrollTop;
      current_pos.value = pos;
		};
    
    return {go_to,on_scroll,show_menu,display_menu,current_pos,home_dom,about_dom,norms_dom,plans_dom,news_dom,course_dom,contact_dom};
  }
}
</script>

