          <img class="z-1 w-22rem md:w-25rem xl:w-30rem sello-1" src="@/assets/postits/Quienes_Somos/sellos-01.png">
          <img class="z-2 w-22rem md:w-25rem xl:w-30rem sello-2" src="@/assets/postits/Quienes_Somos/sellos-02.png">
