<script>
import {ref,watchEffect,onMounted} from 'vue';

export default {
  name: 'about',
  props: {
    position:Number,
    about_pos:Number
	},
  setup(props){

    onMounted(() => {
			vision_dom.value = document.getElementById("vision").offsetTop;
      mision_dom.value = document.getElementById("mision").offsetTop;
      valores_dom.value = document.getElementById("valores").offsetTop;
      sellos_dom.value = document.getElementById("sellos").offsetTop;
      bottom_dom.value = document.getElementById("sellos").offsetHeight;
		});

    const vision_dom = ref(0);
    const mision_dom = ref(0);
    const valores_dom = ref(0);
    const sellos_dom = ref(0);

    const bottom_dom = ref(0);

    const vision = ref(false);
    const mision = ref(false);
    const valores = ref(false);
    const sellos = ref(false);

    watchEffect(() => {
      switch(true){
        case props.position >= (vision_dom.value + props.about_pos - 300) && props.position < (mision_dom.value + props.about_pos - 300):
          vision.value = true;
          mision.value = false;
          valores.value = false;
          sellos.value = false;
          break
        case props.position >= (mision_dom.value + props.about_pos - 300) && props.position < (valores_dom.value + props.about_pos - 300):
          vision.value = false;
          mision.value = true;
          valores.value = false;
          sellos.value = false;
          break
        case props.position >= (valores_dom.value + props.about_pos - 300) && props.position < (sellos_dom.value + props.about_pos - 300):
          vision.value = false;
          mision.value = false;
          valores.value = true;
          sellos.value = false;
          break
        case props.position >= (sellos_dom.value + props.about_pos - 300) && props.position < (sellos_dom.value + props.about_pos + bottom_dom.value - 300):
          vision.value = false;
          mision.value = false;
          valores.value = false;
          sellos.value = true;
          break
        default:
          vision.value = false;
          mision.value = false;
          valores.value = false;
          sellos.value = false;
      }
    });
    
    return{vision,mision,valores,sellos}
  }
}

</script>

