<template>
  <div class="fondo py-8">
    <transition name="fade">
      <img v-if="home" class="kid-a w-12rem md:w-21rem lg:w-25rem" src="@/assets/kids/kid-02.png"/>
    </transition>
    <transition name="fade">
      <img v-if="home" class="kid-b w-12rem md:w-21rem lg:w-25rem" src="@/assets/kids/kid-03.png"/>
    </transition>
    <div id="home" class="main-content relative">
      <transition name="fade">
        <div v-if="home">
          <h1 class="main-txt select-none text-4xl md:text-5xl lg:text-6xl xl:text-7xl">Bienvenidos</h1>
          <p class="main-txt mt-5 select-none text-lg md:text-2xl lg:text-3xl xl:text-4xl font-light">A nuestra Escuela Especial de Lenguaje,</p>
          <p class="main-txt select-none text-lg md:text-2xl lg:text-3xl xl:text-4xl font-light">si quieres saber más pincha clic aqui abajo!</p>
          <div @click="$emit('nav_click','news')" class="button-link transition-colors transition-duration-500 cursor-pointer select-none text-xl md:text-2xl lg:text-3xl xl:text-4xl">
              VER MÁS
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import {ref,watchEffect,onMounted} from 'vue';

export default {
  name: 'home',
  emits: ['nav_click'],
  props: {
    position:Number,
    home_pos:Number
	},
  setup(props){

    onMounted(() => {
			home_dom.value = document.getElementById("home").offsetTop;
      bottom_dom.value = document.getElementById("home").offsetHeight;
		});

    const home_dom = ref(0);
    const bottom_dom = ref(0);
    const home = ref(false);

    watchEffect(() => {
      if(props.position < (home_dom.value + props.home_pos + bottom_dom.value + 300)){
        home.value = true;
      } else{
        home.value = false;
      }
    });
    
    return{home}
  }
}

</script>

