<template>
  <div class="fondo py-4">
    <transition name="fade">
      <img v-if="news" class="kid-a hidden md:flex w-13rem lg:w-16rem xl:w-19rem" src="@/assets/kids/kid-10.png"/>
    </transition>
    <div id="noticias" class="main-content relative">
      <transition name="fade">
        <h1 v-if="news" class="main-txt select-none text-3xl md:text-5xl lg:text-6xl xl:text-7xl font-light">Noticias</h1>
      </transition>
    </div>
  </div>
</template>

<script>
import {ref,watchEffect,onMounted} from 'vue';

export default {
  name: 'news',
  props: {
    position:Number,
    news_pos:Number
	},
  setup(props){

    onMounted(() => {
			news_dom.value = document.getElementById("noticias").offsetTop;
      bottom_dom.value = document.getElementById("noticias").offsetHeight;
		});

    const news_dom = ref(0);
    const bottom_dom = ref(0);

    const news = ref(false);

    watchEffect(() => {
      if(props.position >= (news_dom.value + props.news_pos - 300) && props.position < (news_dom.value + props.news_pos + bottom_dom.value - 300)){
        news.value = true;
      } else{
        news.value = false;
      }
    });
    
    return{news}
    
  }
}

</script>

