<script>
import {ref,watchEffect,onMounted} from 'vue';

export default {
  name: 'norms',
  props: {
    position:Number,
    norms_pos:Number
	},
  setup(props){

    onMounted(() => {
			norms_dom.value = document.getElementById("normas").offsetTop;
      bottom_dom.value = document.getElementById("normas").offsetHeight;
		});

    const publicPath = ref(process.env.BASE_URL);

    const norms_dom = ref(0);
    const bottom_dom = ref(0);
    const norm = ref(false);

    watchEffect(() => {
      if(props.position >= (norms_dom.value + props.norms_pos - 300) && props.position < (norms_dom.value + props.norms_pos + bottom_dom.value - 300)){
        norm.value = true;
      } else{
        norm.value = false;
      }
    });

    return {norm,publicPath}
    
  }
}

